@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap");

*{
  box-sizing: border-box;
}

body{
  background-color: #22254b;
  color: white;
  font-family: "Popppins", sans-serif;
  margin: 0;
}

.root{
  display: flex;
  flex-direction: column;
}

header{
  background-color: #373b69;
  display: flex;
  padding: 1rem;
  justify-content: flex-end;
}

.search{
  background-color: transparent;
  border: 2px solid #22254b;
  color: white;
  border-radius: 75px;
  font-family: inherit;
  padding: 0.5rem 1.5rem;
}

.search:focus{
  outline: 0;
}

.movie-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.movie {
  background-color: #373b69;
  border-radius: 30px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
  position: relative;
  overflow: hidden;
  margin: 1rem;
  width: 250px;
}

.movie img {
  width: 100%;
}

.movie-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  font-size: medium;
}

.movie-info h3{
  margin: 0;
}

.movie-over{
  background-color: white;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
  position: absolute;
  padding: 1rem;
  color: #22254b;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover .movie-over{
  transform: translateY(0%);
}

.tag{
  background-color: #22254b;
  border-radius: 5px;
  font-weight: bold;
  padding: 1px;
}

.tag.green{
  color:green;
}

.tag.red{
  color:red;
}

.tag.orange{
  color:orange;
}
